export class Song {
  constructor ({id, mid, singer, name, album, duration, image, url}) {
    this.id = id
    this.mid = mid
    this.singer = singer
    this.name = name
    this.album = album
    this.duration = duration
    this.image = image
    this.url = url
  }
}

export function createSong (item) {
  return {
    album: item.albumname,
    duration: item.interval,
    id: item.songid,
    imageUrl: 'https://y.gtimg.cn/music/photo_new/T002R300x300M000' + item.albummid + '.jpg?max_age=2592000',
    mid: item.songmid,
    name: item.songname,
    singer: item.singer[0].name,
    url: 'http://dl.stream.qqmusic.qq.com/C400' + item.songmid + '.m4a?guid=9595680710&vkey=AA43DEF4F578DE6F1D72D73AEE9D5A5F153F13DD63247C5EE9241E76DE00ACC434E9C1186CA9FD2D298A702851E83A1FB461263A919EEFA5&uin=0&fromtag=38'
  }
}



